const utils = require('./utils.js');

$(document).ready(function () {
  var viewport = utils.findBootstrapEnvironment();
  var options = {
    valueNames: [ 'wrestler-name' ],
    page: 10,
    pagination: [{
      paginationClass: "paginationTop",
    }, {
      paginationClass: "paginationBottom",
    }]
  };
  var userList = new List('wrestlers', options);

  $('#search').on("submit", function(event){
    event.preventDefault();
    return false;
  });

  $('#search').on('keyup keypress', function(e) {
    var keyCode = e.keyCode || e.which;
    if (keyCode === 13) {
      e.preventDefault();
      return false;
    }
  });

  $("#search").on('awesomplete-selectcomplete',function(){
    userList.search(this.value);
  });

  var myLazyLoad = new LazyLoad();

  userList.on('updated', function(thisList){
    myLazyLoad.update();
  });

  if (viewport == "xs") {
    $('#search').on('focus', function() {
      document.body.scrollTop += this.getBoundingClientRect().top - 10
    });
  }

  $('#search').on('input propertychange', function() {
    var $this = $(this);
    var visible = Boolean($this.val());
    $('.form-control-clear').toggleClass('hidden', !visible);
  }).trigger('propertychange');

  $('.form-control-clear').click(function() {
    userList.search('');
    $('#search').val('').trigger('propertychange').focus();
  });
});



